import { AbstractCellHandler } from './handler.abstract';

/**
 * 设置行高列宽
 */
export class CellSetSizeHandler extends AbstractCellHandler {
    public eventNames = ['CELL_SET_SIZE'];

    public name = 'CELL_SET_SIZE';

    public merger = 'uniqe';

    public action({ x, y, width, height }: TableCellSetSizeHandler) {
        const pageResults = this.tableData;

        if (BI.isNull(x) && BI.isNull(y)) {
            return;
        }

        if (!BI.isNull(width)) {
            pageResults.forEach(rowCells => {
                rowCells[x].celldimension.width = width;
            });
        }

        if (!BI.isNull(height)) {
            pageResults[y].forEach(cell => {
                cell.celldimension.height = height;
            });
        }

        this.externalContext.populate(pageResults);
    }
}

export type TableCellSetSizeHandler = Parameters<AbstractCellHandler['action']>[0]
